<template>
  <div>
    <Row type="flex" align="middle">
      <Col class="overtext" span="18" ref="content">{{ row.content }}</Col>
      <Col span="6" v-show="show">
        <a @click="more()" style="padding-left:6px;">更多>></a>
      </Col>
    </Row>
  </div>
</template>

<script>
export default {
    props: {
        row: {
            type: Object,
            default: () => {}
        }
    },
    watch: {
        row(newval, oldval) {
            this.$nextTick(() => {
                const elem = this.$refs.content.$el
                if (elem.clientHeight > 42) {
                    this.show = true
                } else {
                    this.show = false
                }
            })
        }
    },
    data() {
        return {
            show: false
        }
    },
    methods: {
        more() {
            this.$Modal.info({
                title: '详情',
                // content: JSON.stringify(this.row.param),
                render: (h, params) => {
                    return h('Input', {
                        props: {
                            // disabled: true,
                            type: 'textarea',
                            value: JSON.stringify(this.row.content, null, 2),
                            rows: 16
                        }
                    })
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.overtext {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
</style>
